.card-links {
    display: flex;
    justify-content: space-between;
    font-size: 16px;
  }
  .card-link {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: clamp(0.875rem, 0.83rem + 0.23vw, 1rem);
    color: var(--color-burger-menu-default);
  }
  .icon-link{
    width: 20px;
    height: 20px;
    stroke: var(--color-burger-menu-default);
    fill: transparent;
  }
  .icon-git{
    width: 20px;
    height: 20px;
    fill: var(--color-burger-menu-default);
  }